.header__toggle {
  cursor: pointer;
  content: "";
  position: absolute;
  border: none;
  padding: 0;
  width: 90px;
  height: 66px;
  background-color: transparent;
  z-index: 10;
  right: 0;
  top: 0;
}
@media (min-width: 660px) {
  .header__toggle {
    top: 22px;
  }
}
@media (min-width: 1200px) {
  .header__toggle {
    display: none;
  }
}

.header--nojs .header__toggle {
  display: none;
}

.header--nav-closed .header__toggle::before {
  content: "";
  position: absolute;
  width: 50px;
  top: 21px;
  right: 20px;
  height: 4px;
  border-radius: 1px;
  background-color: #ffffff;
  box-shadow: 0 10px 0 0 #ffffff, 0 20px 0 0 #ffffff;
}
.header--nav-closed .header__toggle:hover::before {
  background-color: #d22856;
  box-shadow: 0 10px 0 0 #d22856, 0 20px 0 0 #d22856;
}
.header--nav-closed .header__toggle:active::before {
  opacity: 0.3;
}

.header--nav-opened .header__toggle::before, .header--nav-opened .header__toggle::after {
  content: "";
  position: absolute;
  width: 29px;
  height: 3px;
  background-color: #ffffff;
  top: 33px;
}
.header--nav-opened .header__toggle::before {
  transform: rotate(45deg);
}
.header--nav-opened .header__toggle::after {
  transform: rotate(-45deg);
}
.header--nav-opened .header__toggle:hover::before, .header--nav-opened .header__toggle:hover::after {
  background-color: #d22856;
}
.header--nav-opened .header__toggle:active::before, .header--nav-opened .header__toggle:active::after {
  opacity: 0.3;
}

