.breadcrumbs__item {
  box-sizing: content-box;
  padding: 0;
  width: 6px;
  height: 6px;
  border: none;
  border-radius: 50%;
  background-color: rgba(0, 0, 0, 0.3);
}
.breadcrumbs__item:hover:not(.breadcrumbs__item--current) {
  background-color: rgba(40, 54, 69, 0.6);
}
.breadcrumbs__item:active:not(.breadcrumbs__item--current) {
  background-color: rgba(40, 54, 69, 0.1);
}
.breadcrumbs__item--current {
  background-color: #ffffff;
  border: 3px solid #283645;
}

.control {
  display: none;
  position: relative;
  padding-bottom: 34px;
  padding-top: 34px;
  padding-left: 20px;
  padding-right: 20px;
}
.control--active {
  display: block;
}
@media (min-width: 660px) {
  .control {
    display: block;
    padding-left: 52px;
    position: relative;
  }
  .control::before {
    content: "";
    position: absolute;
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
    left: 0;
    top: 20px;
  }
  .control--crop::before {
    width: 22px;
    height: 22px;
    background-image: url("../img/crop_tablet.svg");
  }
  .control--fill::before {
    width: 24px;
    height: 22px;
    background-image: url("../img/fill__tablet.svg");
  }
  .control--contrast::before {
    width: 16px;
    height: 16px;
    background-image: url("../img/contrast__tablet.svg");
  }
}
@media (min-width: 1200px) {
  .control {
    padding-right: 40px;
    padding-left: 90px;
  }
  .control:not(:last-child) {
    border-bottom: 1px solid rgba(40, 54, 69, 0.1);
  }
  .control--crop::before {
    width: 44px;
    height: 44px;
  }
  .control--fill::before {
    width: 48px;
    height: 43px;
  }
  .control--contrast::before {
    width: 32px;
    height: 32px;
  }
}

.control__bar {
  height: 2px;
  background-color: #283645;
}

.control__switcher {
  background-color: #ffffff;
  border: 2px solid #d22856;
  width: 14px;
  height: 14px;
  border-radius: 50%;
  position: absolute;
  left: 80%;
  top: 27px;
}
.control__switcher:focus {
  background-color: #d22856;
}

.tools__list {
  display: flex;
  justify-content: center;
  gap: 35px;
  padding-top: 40px;
}
@media (min-width: 660px) {
  .tools__list {
    display: none;
  }
}

.tools__item {
  border: none;
  padding: 0;
  fill: rgba(40, 54, 69, 0.3);
}
.tools__item--active {
  fill: #d22856;
}